import { expose } from 'comlink';
import { EdgeDefinition, NodeDefinition } from 'cytoscape';
import { Species } from '../../app/';

export default {} as typeof Worker & { new (): Worker };

export type GraphWorker = {
    nodes: NodeDefinition[];
    edges: EdgeDefinition[];
    setGraph(nodes: NodeDefinition[], edges: EdgeDefinition[]): void;
    generateGraph(organizations: number[][], speciesList: Species[], callback:(nodes: NodeDefinition[], edges: EdgeDefinition[],error: any | undefined) => void): void;
    getOrgsSmallerContaining (organizations: number[][], speciesList: Species[]): {edges: number[][][], map: Map<string, Set<Species>>};
    isSuperset(set: Set<any>, subset: Set<any>): boolean;
    isEqual(A: Set<any>, B: Set<any>): boolean;
}

const graphAPI: GraphWorker = {
    nodes: [],
    edges: [],
    setGraph(nodes: NodeDefinition[], edges: EdgeDefinition[]) {
        this.nodes = nodes;
        this.edges = edges;
    },
    generateGraph(organizations: number[][], speciesList: Species[], callback:(nodes: NodeDefinition[], edges: EdgeDefinition[],error: any | undefined) => void) {
        let _error: any | undefined = undefined;
        if((this.nodes === undefined || this.nodes.length === 0) && (this.edges===undefined || this.edges.length === 0)) {
            try {
                const counter: number[] = new Array(organizations.length).fill(0);

                for(let i = 0; i < organizations.length; i++) {
                    counter[organizations[i].length] = counter[organizations[i].length] ? counter[organizations[i].length]+1 : 1;
                }

                let index = 1;
                for(const organization of organizations) {
                    let allspecieswidth = 20;
                    let all_species = '';
                    if(organization.length === 0) {
                        all_species = '{}, ';
                    }
                    for(let i = 0; i < organization.length; i++) {
                        allspecieswidth += speciesList[organization[i]].id.length*12;
                        all_species += speciesList[organization[i]].id+ ', ';
                    }
                    
                    const vert = organization.length;
                    const orgObject = {
                        data: {id: `O${index}`, label: `O${index}: ${vert}`, normalWidth: 50, all_species: all_species.substring(0, all_species.length-2), all_species_width: allspecieswidth, novelty_species: '', novelty_species_width: 20, rank: vert, height: 20, color: '#000', },
                        rank: vert,
                    };

                    this.nodes.push(orgObject);
                    index++;
                }

                const {edges, map} = this.getOrgsSmallerContaining(organizations, speciesList);

                for (const edgeList of edges) {
                    for(const edge of edgeList) {
                        const edgeObject = {
                            data: {id: `O${edge[0]}O${edge[1]}`, source: `O${edge[0]}`, target: `O${edge[1]}`}
                        };
                        this.edges.push(edgeObject);
                    }
                }

                for(const key of map.keys()) {
                    const node = this.nodes.find(element => element.data.id === key);
                    const index = this.nodes.findIndex(element => element.data.id === key);
                    if(node && index) {
                        const set: Set<Species> | undefined = map.get(key);
                        if(set) {
                            let novspecieswidth = 20;
                            let nov_species = '';
                            if(set.size === 0) {
                                nov_species = ', ';
                            }
                            for(const species of set) {
                                novspecieswidth += species.id.length*12;
                                nov_species += species.id+ ', ';
                            }
                            node.data['novelty_species'] = nov_species.substring(0, nov_species.length-2);
                            node.data['novelty_species_width'] = novspecieswidth;
                        }
                        this.nodes[index] = node;
                    }
                }
            } catch(err: any) {
                _error = err;
                console.error(err);
            }
        }
        callback(this.nodes, this.edges, _error);
    },
    getOrgsSmallerContaining (organizations: number[][], speciesList: Species[]) {
        const result: number[][][] = [];
        
        const map: Map<string, Set<Species>> = new Map<string, Set<Species>>();

        let possibleSolutions: Set<number>[][] = [];
        
        let index: number = 1;
        for (const org of organizations) {
            const set = new Set(org);
            const smallerSets: Set<number>[] = [];
            for (const otherOrg of organizations) {
                const otherSet = new Set(otherOrg);
                if(org.length > otherOrg.length && this.isSuperset(set, otherSet)) {
                    smallerSets.push(otherSet);
                }
            }
            possibleSolutions.push(smallerSets);

            let _set: Set<Species> = new Set();
            for(let i = 0; i < org.length; i++) {
                _set.add(speciesList[org[i]]);
            }

            map.set(`O${index}`, _set);
            index++;
        }
        // console.log(possibleSolutions);
        
        for (let i = 0; i < possibleSolutions.length; i++) {
            const toCheck = possibleSolutions[i];
        
            const _edges: number[][] = [];
        
            for (const set of toCheck) {
                let hasSuper = false;
                for (const _set of toCheck) {
                    if(!this.isEqual(set, _set)) {
                        if(this.isSuperset(_set, set)) {
                            hasSuper = true;
                        }
                    }
                }
                if(!hasSuper) {
                    let bigSet = map.get(`O${i+1}`);
                    let org = organizations.find(element => this.isEqual(new Set(element), set));
                    let smallSet: Set<Species> = new Set();
                    if(org) {
                        for(let i = 0; i < org.length; i++) {
                            smallSet.add(speciesList[org[i]]);
                        }
                    
                        for(const species of smallSet) {
                            if(bigSet?.has(species)) {
                                bigSet.delete(species);
                            }
                        }
                    }
                    if(bigSet) {
                        map.set(`O${i+1}`, bigSet);
                    }
                    _edges.push([i+1,organizations.findIndex(element => this.isEqual(new Set(element), set))+1]);
                }
            }
            result.push(_edges);
        }
        return {edges: result, map: map};
    },
    isSuperset(set: Set<any>, subset: Set<any>): boolean {
        for (const element of subset) {
            if(set.has(element) === false) {
                return false;
            }
        }
        return true;
    },
    isEqual(A: Set<any>, B: Set<any>): boolean {
        if (A.size !== B.size) {
            return false;
        }
        for (const a of A) {
            if (B.has(a) === false) {
                return false;
            }
        }
        return true;
    }
}

expose(graphAPI)
